<template>
  <div class>
    <!--  -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="times"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="first_cateid">
          <el-select v-model="form.first_cateid" placeholder="" @change="changeSortList()">
            <!--  -->
            <el-option
              v-for="item in sortList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="second_cateid">
          <el-select v-model="form.second_cateid" placeholder="" @change="changeGoodsname()">
            <!--  -->
            <el-option
              v-for="item in sortListChildren"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="goodsid">
          <el-select v-model="form.goodsid" placeholder="">
            <!--  -->
            <el-option
              v-for="item in goodsname"
              :key="item.id"
              :label="item.goodsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')"> </el-button>
        <el-button type="primary" @click="add('form')" v-if="info.isAdd"> </el-button>
        <el-button type="primary" @click="update" v-else> </el-button>
      </div>
    </el-dialog>
  </div>
</template> 
<script>
import { filterTime } from "../../../filters/filterTime";
import { successAlert, warningAlert } from "../../../util/alert";
import { reg } from "../../../util/reg";
import { mapGetters, mapActions } from "vuex";
import {
  requestSeckAdd,
  requestSeckDetail,
  requestGoodsList,
  requestSeckUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "seck/list",
      goodsList: "goods/list",
      // 
      sortList: "sort/list",
    }),
  },
  data() {
    return {
      // 
      sortListChildren: [],
      goodsname: [],
      times: [],
      form: {
        title: "",
        begintime: "",
        endtime: "",
        first_cateid: "",
        second_cateid: "",
        goodsid: "",
        status: 1,
      },
      rules: {
        title: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
        first_cateid: [
          { required: true, message: "", trigger: "change" },
        ],
        second_cateid: [
          { required: true, message: "", trigger: "change" },
        ],
        goodsid: [{ required: true, message: "", trigger: "change" }],
      },
    };
  },
  props: ["info"],
  mounted() {
    this.requestSortList();
  },
  methods: {
    //   vuex
    ...mapActions({
      requestSortList: "sort/requestList",
      requestList: "seck/requestList",
    }),
    // 
    changeSortList(bool) {
      let index = this.sortList.findIndex(
        (item) => item.id == this.form.first_cateid
      );
      this.sortListChildren = this.sortList[index].children;
      //true,second_cateid
      if (!bool) {
        this.form.second_cateid = "";
      }
    },
    // 
    changeGoodsname(bool) {
      requestGoodsList({
        fid: this.form.first_cateid,
        sid: this.form.second_cateid,
      }).then((res) => {
        this.goodsname = res.data.list;
      });
      if (!bool) {
        this.form.goodsid = "";
      }
    },

    //
    empty() {
      // 
      (this.sortListChildren = []),
        // 
        (this.goodsname = []),
        (this.form = {
          title: "",
          begintime: "",
          endtime: "",
          first_cateid: "",
          second_cateid: "",
          goodsid: "",
          status: 1,
        });
      this.times = [];
    },
    // 
    cancel(form) {
      this.$refs.form.resetFields();
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 
          this.form.begintime = this.times[0].getTime();
          this.form.endtime = this.times[1].getTime();
          requestSeckAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(""); //
              this.info.show = false;
              this.empty();
              //   
              this.requestList();
              
              this.cancel();
            } else {
              warningAlert("");
            }
          });
        } else {
          warningAlert("");
          return false;
        }
      });
    },

    // 
    getDetail(id) {
      requestSeckDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        var a = new Date(Number(res.data.list.begintime));
        var b = new Date(Number(res.data.list.endtime));
        this.times = [a, b];
        this.form.id = id;
        this.changeSortList(true);
        this.changeGoodsname(true);
      });
    },
    // 
    update() {
      this.form.begintime = Date.parse(new Date(this.times[0]));
      this.form.endtime = Date.parse(new Date(this.times[1]));
      requestSeckUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped >
</style>


