<template>
  <div class>
    <!--  -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="pid">
          <el-select v-model="form.pid">
            <el-option label="----" value disabled></el-option>
            <el-option label="" :value="0"></el-option>
            <!--  -->
            <el-option v-for="item in list" :key="item.id" :label="item.title" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-radio v-model="form.type" :label="1"></el-radio>
          <el-radio v-model="form.type" :label="2"></el-radio>
        </el-form-item>
        <el-form-item label="" v-if="form.type==1" prop="icon">
          <el-select v-model="form.icon">
            <el-option label="----" value disabled></el-option>
            <el-option v-for="item in icon" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" v-else prop="url">
          <el-select v-model="form.url">
            <el-option label="----" value disabled></el-option>
            <el-option v-for="item in urls" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="add" v-if="info.isAdd"> </el-button>
        <el-button type="primary" @click="update" v-else> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestMenuAdd,
  requestMenuDetail,
  requestMenuUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "menu/list",
    }),
  },
  data() {
    return {
      //
      icon: [
        "el-icon-setting",
        "el-icon-s-help",
        "el-icon-s-operation",
        "el-icon-s-grid",
      ],
      //
      urls: [
        "/menu",
        "/role",
        "/admi",
        "/sort",
        "/norms",
        "/goods",
        "/member",
        "/banner",
        "/seck",
      ],
      form: {
        pid: 0,
        title: "",
        icon: "",
        type: 1,
        url: "",
        status: 1,
      },
      rules: {
        title: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 5, message: " 2  5 ", trigger: "blur" },
        ],
        pid: [{ required: true, message: "", trigger: "change" }],
        url: [{ required: true, message: "", trigger: "change" }],
        icon: [{ required: true, message: "", trigger: "change" }],
      },
    };
  },
  props: ["info"],
  mounted() {},
  methods: {
    //   vuex
    ...mapActions({
      requestList: "menu/requestList",
    }),

    //
    empty() {
      this.form = {
        pid: 0,
        title: "",
        icon: "",
        type: 1,
        url: "",
        status: 1,
      };
    },
    // 
    cancel() {
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          requestMenuAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(""); //
              this.info.show = false;
              this.cancel();
              this.empty();
              //   
              this.requestList();
            }
          });
        } else {
          warningAlert("");
          return false;
        }
      });
    },

    // 
    getDetail(id) {
      requestMenuDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
      });
    },

    // 
    update() {
      requestMenuUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped >
</style>


