<template>
  <div class="home">
    <div id="main" style="width: 800px;height:400px;"></div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {};
  },
  mounted() {
    // domecharts
    var myChart = this.$echarts.init(document.getElementById("main"));

    // 
    var option = {
      title: {
        text: "",
      },
      tooltip: {},
      legend: {
        data: [""],
      },
      xAxis: {
        data: ["", "", "", "", "", ""],
      },
      yAxis: {},
      series: [
        {
          name: "",
          type: "line",
          data: [5, 20, 36, 10, 10, 20],
        },
      ],
    };

    // 
    myChart.setOption(option);
  },
};
</script>

<style scoped >
.home {
  width: 100%;
  height: 100%;
  /* display: flex; */
}
</style>


