<template>
  <div class="norms">
    <!--  -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="specsname">
          <el-input v-model="form.specsname"></el-input>
        </el-form-item>
        <el-form-item label="" v-for="(item,index) in attrArr" :key="index">
          <el-input v-model="item.value"></el-input>
          <el-button type="primary" @click="addAttr" v-if="index==0"></el-button>
          <el-button type="danger" @click="delAttr(index)" v-else></el-button>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="add" v-if="info.isAdd"> </el-button>
        <el-button type="primary" @click="update" v-else> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestNormsAdd,
  requestNormsDetail,
  requestNormsUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "norms/list",
    }),
  },
  data() {
    return {
      form: {
        specsname: "",
        status: 1,
      },
      rules: {
        specsname: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" },
        ],
      },
      attrArr: [
        {
          value: "",
        },
      ],
    };
  },
  props: ["info"],
  mounted() {},
  methods: {
    //   vuex
    ...mapActions({
      requestList: "norms/requestList",
      requestTotal: "norms/requestTotal",
    }),

    //
    empty() {
      this.form = {
        specsname: "",
        status: 1,
      };
      this.attrArr = [
        {
          value: "",
        },
      ];
    },

    // 
    cancel() {
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 
    addAttr() {
      this.attrArr.push({ value: "" });
    },
    // 
    delAttr(index) {
      this.attrArr.splice(index, 1);
    },

    // 
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.attrArr.some((item) => item.value == "")) {
            warningAlert("");
            return;
          }
          this.form.attrs = JSON.stringify(
            this.attrArr.map((item) => item.value)
          );
          requestNormsAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg); //
              this.info.show = false;
              this.cancel();
              this.empty();
              //   
              this.requestList();
              //
              this.requestTotal();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("");
          return false;
        }
      });
    },

    // 
    getDetail(id) {
      requestNormsDetail({ id: id }).then((res) => {
        this.form = res.data.list[0];
        this.attrArr = JSON.parse(res.data.list[0].attrs).map((item) => ({
          value: item,
        }));
      });
    },

    // 
    update() {
      if (this.attrArr.some((item) => item.value == "")) {
        warningAlert("");
        return;
      }
      this.form.attrs = JSON.stringify(this.attrArr.map((item) => item.value));
      requestNormsUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped long='stylus'>
.norms >>> .el-form-item__content {
  display: flex;
}
.norms >>> .el-button {
  width: auto;
}
.norms >>> .el-input {
  flex: 1;
}
</style>


