<template>
  <div class>
    <!--  -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="roleid">
          <el-select v-model="form.roleid">
            <el-option label="----" value disabled></el-option>
            <!--  -->
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.rolename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input placeholder="" v-model="form.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="add(form)" v-if="info.isAdd"> </el-button>
        <el-button type="primary" @click="update" v-else> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestAdmiAdd,
  requestAdmiDetail,
  requestAdmiUpdate,
} from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      roleList: "role/list",
      list: "admi/list",
    }),
  },
  data() {
    return {
      form: {
        username: "",
        password: "",
        roleid: "",
        status: 1,
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        roleid: [
          { required: true, message: "", trigger: "change" },
        ],
        password: [
          { required: true, message: "", trigger: "change" },
        ],
      },
    };
  },
  props: ["info"],
  mounted() {
    //menulist
    if (this.roleList.length === 0) {
      this.requestRoleList();
    }
  },
  methods: {
    //   vuex
    ...mapActions({
      requestList: "admi/requestList",
      requestRoleList: "role/requestList",
      requestTotal: "admi/requestTotal",
    }),

    //
    empty() {
      this.form = {
        username: "",
        password: "",
        roleid: "",
        status: 1,
      };
    },

    // 
    cancel() {
      this.$refs.form.resetFields();
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          requestAdmiAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg); //
              this.info.show = false;
              this.cancel();
              this.empty();
              //   
              this.requestList();
              //
              this.requestTotal();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("");
          return false;
        }
      });
    },

    // 
    getDetail(id) {
      requestAdmiDetail({ uid: id }).then((res) => {
        this.form = res.data.list;
        this.form.password = "";
      });
    },

    // 
    update() {
      requestAdmiUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped >
</style>


