<template>
  <div class>
    <!--  -->
    <el-dialog :title="info.title" :visible.sync="info.show">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="rolename">
          <el-input v-model="form.rolename"></el-input>
        </el-form-item>
        <el-tree
          :data="Menulist"
          show-checkbox
          node-key="id"
          :default-checked-keys="defaultKey"
          :props="defaultProps"
          ref="tree"
          prop="menus"
        ></el-tree>
        <el-form-item label="">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="add(form)" v-if="info.isAdd"> </el-button>
        <el-button type="primary" @click="update" v-else> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestRoleAdd,
  requestRoleDetail,
  requestRoleUpdate,
} from "../../../util/request";
export default {
  computed: {
    //   vuex
    ...mapGetters({
      list: "role/list",
      Menulist: "menu/list",
    }),
  },
  data() {
    return {
      form: {
        status: 1,
        rolename: "",
        menus: "",
      },
      rules: {
        rolename: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 6, message: " 2  6 ", trigger: "blur" },
        ],
      },
      //
      defaultProps: {
        children: "children",
        label: "title",
      },
      defaultKey: [],
    };
  },
  props: ["info"],
  mounted() {
    //menulist
    if (this.Menulist.length === 0) {
      this.requestMenuList();
    }
  },

  methods: {
    //   vuex
    ...mapActions({
      requestList: "role/requestList",
      requestMenuList: "menu/requestList",
    }),

    //
    empty() {
      this.form = {
        status: 1,
        rolename: "",
        menus: "",
      };
      this.$refs.tree.setCheckedKeys([]);
    },
    // 
    cancel() {
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
          if (this.form.menus.length - 2 == 0) {
            warningAlert("");
            return;
          }
          // 
          requestRoleAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg); //
              this.info.show = false;
              this.cancel();
              this.empty();
              //   
              this.requestList();
              // 
              this.$refs[form].resetFields();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("");
          return false;
        }
      });
    },

    // 
    getDetail(id) {
      requestRoleDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.defaultKey = JSON.parse(res.data.list.menus);
      });
    },

    // 
    update() {
      this.form.menus = JSON.stringify(this.$refs.tree.getCheckedKeys());
      requestRoleUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped >
</style>


