<template>
  <div class>
    <!--  -->
    <el-table
      :data="list"
      style="width: 100%,margin-bottom:20px"
      row-key="id"
      border
      :tree-props="{children: 'children'}"
    >
      <el-table-column prop="id" label="" width="80"></el-table-column>
      <el-table-column prop="rolename" label="" width="180"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status==1"></el-button>
          <el-button type="info" v-else></el-button>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)"></el-button>
          <el-button type="danger" @click="del(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { requestRoleDelete } from "../../../util/request";
export default {
  computed: {
    ...mapGetters({
      list: "role/list",
    }),
  },
  data() {
    return {};
  },  
  mounted() {
    this.requestList();
  },
  methods: {
    ...mapActions({
      requestList: "role/requestList",
    }),

    // 
    edit(id) {
      this.$emit("edit", id);
    },
    // 
    del(id) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          requestRoleDelete({ id: id }).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg);
              this.requestList();
            } else {
              warningAlert(res.data.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
  },
};
</script>
<style scoped >
</style>


