<template>
  <div class="role">
    <el-button type="primary" @click="add" class="add"> </el-button>
    <!--  -->
    <v-alert :info="info" ref="add"></v-alert>

    <!--  -->
    <!-- <v-list @edit="edit"></v-list> -->
    <v-list @edit="edit"></v-list>
    <!--  -->
  </div>
</template>
<script>
import vList from "./components/vList";
import vAlert from "./components/vAlert";
export default {
  components: {
    vList,
    vAlert,
  },
  data() {
    return {
      info: {
        show: false,
        title: "",
        isAdd: true,
      },
    };
  },
  mounted() {},
  methods: {
    // 
    add(id) {
        (this.info.show = true),
        (this.info.tatle = ""),
        (this.info.isAdd = true);
    },
    //   ()
    edit(id) {
      this.info.show = true;
      this.info.title = "";
      this.info.isAdd = false;
      this.$refs.add.getDetail(id);
    },
  },
};
</script>
<style scoped >
.add {
  margin-bottom: 20px;
}
</style>


