<template>
  <div class="goods">
    <!--  -->
    <el-dialog :title="info.title" :visible.sync="info.show" @opened="establishDesc">
      <el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="first_cateid">
          <el-select v-model="form.first_cateid" placeholder="" @change="changeSortList()">
            <!--  -->
            <el-option
              v-for="item in sortList"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="second_cateid">
          <el-select v-model="form.second_cateid" placeholder="">
            <!--  -->
            <el-option
              v-for="item in sortListChildren"
              :key="item.id"
              :label="item.catename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="goodsname">
          <el-input v-model="form.goodsname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="form.price"></el-input>
        </el-form-item>
        <el-form-item label="" prop="market_price">
          <el-input v-model="form.market_price"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="avatar-uploader"
            action="#"
            :show-file-list="false"
            :on-change="changeImg"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="specsid">
          <el-select v-model="form.specsid" placeholder="" @change="changeNormsList()">
            <!--  -->
            <el-option
              v-for="item in normsList"
              :key="item.id"
              :label="item.specsname"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="specsattr">
          <el-select v-model="form.specsattr" placeholder="">
            <!--  -->
            <el-option v-for="item in normsListAttrs" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="form.isnew" :label="1"></el-radio>
          <el-radio v-model="form.isnew" :label="2"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="form.ishot" :label="1"></el-radio>
          <el-radio v-model="form.ishot" :label="2"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <div id="desc"></div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="add(form)" v-if="info.isAdd"> </el-button>
        <el-button type="primary" @click="update" v-else> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { successAlert, warningAlert } from "../../../util/alert";
import { mapGetters, mapActions } from "vuex";
import { reg } from "../../../util/reg";
import {
  requestGoodsAdd,
  requestGoodsDetail,
  requestGoodsUpdate,
} from "../../../util/request";
import E from "wangeditor";
export default {
  computed: {
    ...mapGetters({
      // 
      normsList: "norms/list",
      // 
      sortList: "sort/list",
    }),
  },
  data() {
    return {
      // 
      sortListChildren: [],
      // 
      normsListAttrs: [],
      imageUrl: "",
      form: {
        first_cateid: "",
        second_cateid: "",
        goodsname: "",
        price: null,
        market_price: null,
        img: null,
        description: "",
        specsid: "",
        specsattr: "",
        isnew: 1,
        ishot: 1,
        status: 1,
      },
      rules: {
        goodsname: [
          { required: true, message: "", trigger: "blur" },
        ],
        price: [{ required: true, message: "", trigger: "blur" }],
        market_price: [
          { required: true, message: "", trigger: "blur" },
        ],
        first_cateid: [
          { required: true, message: "", trigger: "change" },
        ],
        second_cateid: [
          { required: true, message: "", trigger: "change" },
        ],
        specsid: [
          { required: true, message: "", trigger: "change" },
        ],
        specsattr: [
          { required: true, message: "", trigger: "change" },
        ],
      },
    };
  },
  props: ["info"],
  mounted() {
    this.requestSortList();
    //
    this.requestNormsList(true);
  },
  methods: {
    //   vuex
    ...mapActions({
      requestSortList: "sort/requestList",
      requestNormsList: "norms/requestList",
      requestList: "goods/requestList",
      requestTotal: "goods/requestTotal",
    }),
    // 
    changeSortList(bool) {
      let index = this.sortList.findIndex(
        (item) => item.id == this.form.first_cateid
      );
      this.sortListChildren = this.sortList[index].children;
      //true,second_cateid
      if (!bool) {
        this.form.second_cateid = "";
      }
    },
    //
    changeNormsList(bool) {
      let index = this.normsList.findIndex(
        (item) => item.id == this.form.specsid
      );
      this.normsListAttrs = this.normsList[index].attrs;
      if (!bool) {
        this.form.specsattr = "";
      }
    },
    // 
    establishDesc() {
      this.editor = new E("#desc");
      this.editor.create();
      this.editor.txt.html(this.form.description);
    },

    // changeImg
    changeImg(e) {
      // 2M
      if (e.size > 2 * 1024 * 1024) {
        warningAlert("2M");
        return;
      }
      // 
      var file = e.raw;
      this.imageUrl = URL.createObjectURL(file);
      this.form.img = file;
    },

    //
    empty() {
      // 
      (this.sortListChildren = []),
        // 
        (this.normsListAttrs = []),
        // 
        (this.imageUrl = ""),
        (this.form = {
          first_cateid: "",
          second_cateid: "",
          goodsname: "",
          price: 0,
          market_price: 0,
          img: null,
          description: "",
          specsid: "",
          specsattr: "",
          isnew: 1,
          ishot: 1,
          status: 1,
        });
      this.editor.txt.html("");
    },

    // 
    cancel() {
      this.$refs.form.resetFields();
      this.info.show = false;
      if (!this.info.isAdd) {
        this.empty();
      }
    },

    // 
    add(form) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          // 
          if (this.form.img === null) {
            warningAlert("");
            return;
          }
          // 
          this.form.description = this.editor.txt.html();
          if (this.form.description == "<p><br></p>") {
            warningAlert("");
            return;
          }
          this.form.specsattr = JSON.stringify(this.form.specsattr);
          requestGoodsAdd(this.form).then((res) => {
            if (res.data.code == 200) {
              successAlert(res.data.msg); //
              this.info.show = false;
              this.cancel();
              this.empty();
              //   
              this.requestList();
              //
              this.requestTotal();
            } else {
              warningAlert(res.data.msg);
            }
          });
        } else {
          warningAlert("");
          return false;
        }
      });
    },

    // 
    getDetail(id) {
      requestGoodsDetail({ id: id }).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.imageUrl = this.$imgPre + this.form.img;
        //
        this.changeSortList(true);
        //
        this.changeNormsList(true);
        this.form.specsattr = JSON.parse(this.form.specsattr);
      });
    },

    // 
    update() {
      this.form.description = this.editor.txt.html();
      this.form.specsattr = JSON.stringify(this.form.specsattr);
      requestGoodsUpdate(this.form).then((res) => {
        if (res.data.code == 200) {
          successAlert(res.data.msg);
          this.empty();
          this.cancel();
          //   
          this.requestList();
        } else {
          warningAlert(res.data.msg);
        }
      });
    },
  },
};
</script>
<style scoped long='stylus'>
.goods >>> .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.goods >>> .el-upload:hover {
  border: 1px dashed #409eff;
}

.goods >>> .avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}

.goods >>> .avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


